.header {
  position: relative;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}

.header__nav {
  position: relative;
  background-image: url("../../public/img/header/nav-menu-bg.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  max-width: 840px;
  max-height: 106px;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 28px;
  margin: 48px auto 0;

  &::before,
  &::after {
    content: "";
    position: absolute;
    height: 60px;
    width: 64px;
    background: url("../../public/img/header/nav-arrow.png");
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center center;
  }

  &::before {
    left: 10px;
    top: 25px;
  }

  &::after {
    transform: scale(-1, -1);
    right: 14px;
    top: 28px;
  }
}

.header__nav-links {
  max-width: 70%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.header__nav-link-text {
  position: relative;
  background: linear-gradient(180deg, #216933 0%, #e9ec42 100%), #ffffff;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;

  &::after {
    content: "";
    position: absolute;
    background-color: #ffffffd6;
    max-width: 60%;
    width: 0;
    height: 2px;
    border-radius: 2px;
    right: 0;
    bottom: -3px;
  }

  &:hover {
    color: #ffffffd6;
    -webkit-text-fill-color: #ffffffd6;

    &::after {
      width: 100%;
      transition: 0.6s all;
    }
  }
}

.header__nav-link {
  position: relative;
  line-height: 1;

  &::after {
    content: "";
    position: absolute;
    background-color: #ffffffd6;
    max-width: 95%;
    width: 0;
    height: 2px;
    border-radius: 2px;
    right: 0;
    bottom: 0px;
  }

  &:hover {
    svg {
      path {
        fill: rgb(218, 218, 218);
      }
    }

    &::after {
      width: 100%;
      transition: all 0.6s;
    }
  }
}

.header__nav-link {
  svg {
    width: 46px;
    height: 36px;
  }
}

.header__nav-link-text--active {
  color: #ffffffd6;
  -webkit-text-fill-color: #ffffffd6;

  &::after {
    content: "";
    position: absolute;
    background-color: #ffffffd6;
    max-width: 60%;
    width: 100%;
    height: 2px;
    border-radius: 2px;
    right: 0;
    bottom: -3px;
  }
}

.header__nav-link-img {
  margin-bottom: -4px;
}

.header__nav-logo {
  width: 78px;
  height: 56px;
  object-fit: contain;
  margin-top: -5px;
}

.header__bg-video {
  position: absolute;
  z-index: -1;
  top: -5%;
  display: block;
  width: 100%;
}
